.user-list__item:hover .user-list__link,
.user-list__item:focus .user-list__link {
    opacity: 0.6;
}

.user-list__item:active .user-list__link {
    opacity: 0.3;
}

.user-list__link {
    padding-left: 5px;
    display: flex;
    align-items: center;
}

.user-list__link-icon {
    margin-right: 25px;
    fill: $black-2;
    opacity: 0.2;
}

.user-list__item:hover .user-list__link-icon,
.user-list__item:focus .user-list__link-icon {
    fill: $seafoam-blue;
    opacity: 1;
}

.user-list__item:active .user-list__link-icon {
    fill: $seafoam-blue;
    opacity: 0.3;
}

.user-list {
    margin: 0;
    padding-left: 0;
}

.user-list__item {
    padding: 27px 9px 27px 75px;

    border-top: 1px solid $white-2;
}

.user-list__item--search,
.user-list__item--cart {
    padding-left: 25px;
}

@media (min-width: 768px) {
    .user-list {
        flex-basis: 54%;
        padding-right: 30px;

        display: flex;
        justify-content: flex-end;
    }

    .user-list__item {
        padding-bottom: 35px;
        padding-right: 20px;

        border-top: none;
        border-left: 1px solid $white-2;
    }

    .user-list__link-icon {
        margin-top: 7px;
        margin-right: 13px;
    }

    .user-list__link-icon--cart {
        margin-right: 30px;
    }

    .user-list__link-note {
        display: none;
    }
}

@media (min-width: 1150px) {
    .user-list {
        flex-basis: 40%;
        order: 3;
    }
}
