.catalog-main {
    margin: 0;
    margin-top: 125px;
    padding: 0;
}

.catalog-main__title {
    width: 110px;
    margin: 0;

    font-family: "Open Sans", "Tahoma", sans-serif;
    font-size: 19px;
    font-weight: bold;

    line-height: 1.3;

    color: $white;
}

.catalog-main__title:active {
    opacity: 0.3;
}

.catalog-main__link {
    position: relative;

    margin: 0 auto;
    padding: 27px 185px 27px 28px;

    display: block;
    transition: 0.1s;
}

.catalog-main__link:hover {
    padding: 22px 185px 32px 28px;
}

.catalog-main__link--interior::before {
    position: absolute;
    right: 35px;
    content: url("../img/icon-interior.svg");
    opacity: 0.5;
}

.catalog-main__link--toys::before {
    position: absolute;
    right: 25px;
    content: url("../img/icon-toy.svg");
    opacity: 0.5;
}

.catalog-main__link--interior:hover::before,
.catalog-main__link--interior:focus::before,
.catalog-main__link--toys:hover::before,
.catalog-main__link--toys:focus::before {
    opacity: 0.7;
}

.catalog-main__link--interior:active::before,
.catalog-main__link--toys:active::before {
    opacity: 0.3;
}

.catalog-main__item:hover {
    transform: scaleY(1.05);
}

.catalog-main__item--interior {
    background-color: $seafoam-blue;
}

.catalog-main__item--interior:hover,
.catalog-main__item--interior:focus {
    background-color: $seafoam-blue-1;
}

.catalog-main__item--toys {
    background-color: $greeny-blue;
}

.catalog-main__item--toys:hover,
.catalog-main__item--toys:focus {
    background-color: $greeny-blue-1;
}

@media (min-width: 768px) {
    .catalog-main {
        height: 119px;
        position: relative;
        display: flex;
        justify-content: center;
    }

    .catalog-main__item {
        flex-basis: 50%;
        display: flex;
        justify-content: space-between;
    }

    .catalog-main__title {
        margin: 0;

        font-size: 20px;
        line-height: 1.5;
    }
}

@media (min-width: 1150px) {
    .catalog-main__link {
        width: 525px;
        padding-left: 37px;
    }

    .catalog-main__link:hover {
        padding-left: 37px;
    }
}
