.page-inner__title-wrap {
    position: relative;
    padding: 72px 20px;

    background-color: $white-1;
}

.page-inner__title {
    max-width: 1150px;
    margin: 0 auto;

    font-family: "Open Sans", "Tahoma", sans-serif;
    font-size: 27px;
    font-weight: bold;

    line-height: 1.1;
    text-align: center;

    color: $black-1;
}

@media (min-width: 768px) {
    .page-inner__title-wrap {
        margin-bottom: 52px;
    }

    .page-inner__title-wrap::after {
        position: absolute;
        bottom: 0;
        left: 0;
        content: "";

        width: 100%;
        height: 14px;
        background-image: url("../img/bg-zigzag-fill.svg");
    }

    .page-inner__title {
        padding-top: 25px;
        font-size: 43px;
    }
}

@media (min-width: 1150px) {
    .page-inner__title-wrap {
        padding-bottom: 105px;
    }

    .page-inner__title {
        padding-top: 20px;
        font-size: 53px;
    }
}
